<template>
  <div class="main-container">
    <el-row v-if="loading" style="text-align: center;">
      <el-button type="text" class="load-icon" disabled><i class="el-icon-loading" /></el-button>
    </el-row>
    <el-table
      v-if="tableData.length > 0"
      :data="tableData"
    >
      <el-table-column
        label=""
      >
        <template slot-scope="scope">
          <router-link :to="{ name: 'MaterialView', params: { id: scope.row.id }}" class="unline nowrap">{{ scope.row.title }}</router-link>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="createDate"
        width="140"
        class="fr"
      />
    </el-table>
    <el-row v-else>
      <el-col v-if="!loading" :xs="24" :sm="12" :md="8" :lg="6" :xl="4">
        未搜索到相关内容
      </el-col>
    </el-row>
    <Pagination :page-count="pageOption.pageCount" :total-count="pageOption.totalCount" :per-page="pageOption.perPage" />
  </div>
</template>

<script>
import { getList } from '@/api/article'
import { debug } from '@/utils'
import { isEmpty } from '@/utils/validate'
import Pagination from '@/components/Pagination.vue'

export default {
  name: 'Article',
  components: {
    Pagination
  },
  data() {
    return {
      apiUrl: '/material',
      formData: {
        page: 1,
        perPage: 10
      },
      loading: true,
      tableData: [],
      pageOption: {
        totalCount: 0,
        perPage: 10,
        pageCount: 0,
        currentPage: 1
      }
    }
  },
  created() {
    this.search(1)
  },
  methods: {
    headerSearch(value) {
      this.loading = true
      this.formData.title = value
      this.apiUrl = isEmpty(value) ? '/material' : '/search'
      this.search(1)
    },
    search(page) {
      this.loading = true
      this.formData.page = page
      this.tableData = []
      getList(this.formData, this.apiUrl).then(response => {
        if (response.status === 'success') {
          const items = response.data.items
          this.pageOption.totalCount = response.data.page === undefined ? 0 : (response.data.page.totalCount || 0)
          this.pageOption.perPage = response.data.page === undefined ? 0 : (response.data.page.perPage || 10)
          this.pageOption.pageCount = response.data.page === undefined ? 0 : (response.data.page.pageCount || 0)

          if (items.length > 0) {
            for (var i in items) {
              this.tableData.push({
                id: items[i].id,
                title: items[i].title,
                statusName: items[i].statusName,
                authorName: items[i].authorName,
                content: items[i].content,
                createDate: items[i].createDate
              })
            }
          }
          debug(this.tableData)
        }
        this.loading = false
      }).catch(error => {
        debug(error)
        this.loading = false
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.main-container {
  margin-bottom: 50px;

  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }

    .load-icon {
      font-size: 28px;
      color: #389cd6;
    }
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
    height: 160px;
    overflow: auto;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    min-width: 280px;
    margin-top: 10px;

    .title {
      font-weight: bold;
      width: 100%;
      float: left;
      white-space: nowrap;
      overflow: auto;
    }
  }
}
</style>
