<template>
  <el-pagination
    v-if="pageCount > 1"
    class="pagination fr"
    background
    small
    layout="prev, pager, next"
    :total="totalCount"
    :page-size="perPage"
    @current-change="handleCurrentChange"
  />
</template>

<script>
export default {
  name: 'Pagination',
  props: {
    pageCount: {
      type: Number,
      default: 0
    },
    totalCount: {
      type: Number,
      default: 0
    },
    perPage: {
      type: Number,
      default: 0
    }
  },
  methods: {
    handleCurrentChange(currentPage) {
      this.$parent.search(currentPage)
    }
  }
}
</script>

<style lang="scss" scoped>
.pagination {
  margin-top: 10px;
}
</style>
